#mybox{
  font-family: Rockwell; 
}
.home-nav{
  text-decoration: none;
 
}
.btn-color{
  color: white;
  
}
.btn-color:hover{
  color: white;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

