.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@media (max-width: 1200px){
  .flex-container{
    display: grid;
    grid-template-columns: repeat(3, 1fr); /* Set two equal columns */
      gap: 6px;
  }
  .flex-item {
    height: 38px;
}
} 
@media (max-width: 700px){
  .flex-container{
    display: grid;
   grid-template-columns: repeat(2, 1fr); /* Set two equal columns */
     gap: 10px;
  }
} 
@media (max-width: 430px){
  .flex-container{
    display: grid;
   grid-template-columns: repeat(2, 1fr); /* Set two equal columns */
     gap: 6px;
   
  }
  .flex-item {
    height: 38px;
}
  .summ {
    margin-top: 163px;

}
} 